import React from 'react';
import { Carousel } from 'antd';
import ContentBlock from '../../components/ContentBlock';
import Svg7 from '../../assets/Online Groceries-pana.svg';
import Svg8 from '../../assets/Online Groceries-amico.svg';
import Svg9 from '../../assets/Discount-pana.svg';

const Infos = [
  {
    key: 'k1',
    position: 'right',
    heading: 'Unimi Food ti ri y!',
    caption: '',
    paragraph: 'Cn chn ch g m khng t Unimi ngay?',
    link: '/account/login',
    button: 'Cng khm ph',
    img: Svg7,
  },
  {
    key: 'k2',
    position: 'left',
    heading: 'Gim gi khng',
    caption: '',
    paragraph:
      'Nhiu u i mi ngy ang ch i bn ti Unimi y!',
    link: '/',
    button: 't hng ngay',
    img: Svg9,
  },
  {
    key: 'k3',
    position: 'right',
    heading: 'Tha h la chn',
    caption: '',
    paragraph: 'a dng mn ngon trn khp a bn thnh ph! ',
    link: '/',
    button: 't hng ngay',
    img: Svg8,
  },
];
const LandingPage = () => (
  <div className="landing-page-wrapper">
    <Carousel autoplay>
      {Infos.map((info) => (
        <ContentBlock content={info} key={info.key} />
      ))}
    </Carousel>
  </div>
);

export default LandingPage;
